.mobile-navigation__input-registration {
  display: flex;
  min-width: 312px;
  font-family: "Open Sans", sans-serif;
  margin: 16px auto 13px auto;
  padding: 16px 0 16px 16px;
  color: rgba(7, 12, 39, 0.6);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  background-color: #eef1ff;
  border: none;
  border-radius: 12px;
}

.mobile-navigation__input-registration:nth-child(even) {
  margin: 16px auto 5px auto;
}

.mobile-navigation__input-registration:focus {
  outline: 1px #0d2dba solid;
}

.mobile-navigation__input-registration:hover {
  cursor: pointer;
  background-color: #dadffa;
}

@media screen and (max-width: 1025px) {
  .mobile-navigation__input-registration {
    min-width: 440px;
  }

  .mobile-navigation__input-registration:nth-child(even) {
    margin: 16px auto 5px auto;
  }
}

@media screen and (max-width: 760px) {
  .mobile-navigation__input-registration {
    min-width: 480px;
  }
}

@media screen and (max-width: 575px) {
  .mobile-navigation__input-registration {
    margin: 16px 16px 3px 16px;
    min-width: 272px;
    width: calc(100vw - 48px);
  }

  .mobile-navigation__input-registration:nth-child(even) {
    width: calc(100vw - 48px);
  }
}
