/* CSS стили для mobile-navigation */

/* Убираем стандартное поведение полей для ввода в хроме и мозилле */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield;
}

    .mobile-navigation {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .mobile-navigation__nav-list {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      flex-direction: column;
      align-items: center;  
    }
  
    .mobile-navigation__nav-links-container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .mobile-navigation__container {
      max-height: calc(100vh - 104px - 120px);  /* Максимальная высота контейнера с формой */
      overflow-y: auto;
      scrollbar-width: none; /* Для Firefox */
      -ms-overflow-style: none;  /* Для Internet Explorer и Edge */
    }

    .mobile-navigation__container::-webkit-scrollbar {
      display: none; /* Для Chrome, Safari и Opera */
    }
    
    .mobile-navigation__form {
      min-width: 328px;
    }

    .mobile-navigation__title {
      margin: 0;
      padding-top: 16px;
      padding-bottom: 32px;
      width: 328px;
      font-family: "Open Sans", sans-serif;
      font-size: 28px;
      font-weight: 700;
      line-height: 36px;
      text-align: center;
      color: #070C27;
    }

    .mobile-navigation__subtitle {
      display: flex;
      align-items: center;
      margin: 0;
      padding-top: 12px;
      padding-bottom: 12px;
      width:  192px;
      font-family: "Open Sans", sans-serif;
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      text-align: left;
      color: #070C27;
      opacity: .6;
    }

    .mobile-navigation__figure {
      display: flex;
      flex-direction: row;
      justify-content: center;
      margin: 0 auto;
      padding: 0;
      padding-bottom: 24px;
    }
  
    .mobile-navigation__image{
      margin: 0 8px 0 0;
      min-width: 128px;
      max-height: 72px; 
      object-fit: cover;
      border-radius: 8px;
    }

    .mobile-navigation__count-text {
      margin: 6px 0;
      font-family: "Open Sans", sans-serif;
      letter-spacing: -0.1px;
      padding-right: 14px;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      text-align: left;
      color: #070C27;
    }

    .mobile-navigation__count {
      display: flex;
      margin-bottom: 10px;
    }

    .mobile-navigation__count-input {
      margin: 0 12px;
      width: 24px;
      border: 2px #EEF1FF solid;
      border-radius: 8px;
    }

    .mobile-navigation__data {
      display: flex;
      justify-content: space-between;
      padding: 0;
      margin: 0;
      width: 328px;
    }

    .mobile-navigation__data-hours {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      padding: 0;
      margin: 0 auto;
    }

    .mobile-navigation__data-input_type {
      margin: 0 0 12px 0;
      padding: 0;
    }

    .mobile-navigation__data-input_type_button {
      font-family: "Open Sans", sans-serif;
      margin: 0 8px 0 0;
      padding: 12px 16px 16px 16px;
      color: #37489f;
      font-size: 16px;
      line-height: 20px;
      font-weight: 400;
      background-color: #EEF1FF;
      border: none;
      border-radius: 12px;
    }

    .mobile-navigation__data-input_type_button:hover {
      cursor: pointer;
      color: #fff;
      background-color: #6073d5;
    }

    .mobile-navigation__input-text_summary {
      font-family: "Open Sans", sans-serif;
      margin: 0 0 0 5px;
      padding: 0;
      color: rgb(7, 12, 39);
      font-size: 20px;
      line-height: 24px;
      font-weight: 900;
    }

    .mobile-navigation__text-oferta {
      max-width: 328px;
      font-family: "Open Sans", sans-serif;
      letter-spacing: -0.1px;
      margin: 0 45px;
      padding: 5px 0 7px 0;
      color: rgb(7, 12, 39, .6);
      font-size: 10px;
      line-height: 12px;
      font-weight: 400;
    }

    .mobile-navigation__text-oferta_link {
      color: #6073d5;
      margin-left: 2px;
      margin-right: 2px;
    }

  @media screen and (max-width: 1025px) {
    .mobile-navigation__form {
      min-width: 456px;
    }

    .mobile-navigation__title {
      width: 456px;
    }

    .mobile-navigation__subtitle {
      width: 320px;
    }

    .mobile-navigation__data {
      width: 456px;
    }

    .mobile-navigation__figure {
      max-width: 456px;
    }

    .mobile-navigation__text-oferta {
      max-width: 456px;
    }
  }

  @media screen and (max-width: 760px) {
    .mobile-navigation__form {
      min-width: 496px;
    }

    .mobile-navigation__title {
      padding-top: 17px;
      font-size: 24px;
      font-weight: 700;
      line-height: 32px;
    }

    .mobile-navigation__subtitle {
      width: 360px;
    }

    .mobile-navigation__data {
      width: 496px;
    }

    .mobile-navigation__figure {
      max-width: 496px;
    }

    .mobile-navigation__text-oferta {
      margin: 0 25px 0 0;
    }
  }
  
@media screen and (max-width: 575px) {

  .mobile-navigation__container {
    width: 100vw;
  }

  .mobile-navigation__form {
    min-width: 288px;
  }

  .mobile-navigation__title {
    width: 256px;
    padding-bottom: 30px;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
  }

  .mobile-navigation__subtitle {
    min-width: 152px;
    height: 48px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Количество видимых строк */
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    word-wrap: break-word;
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 16px;
    font-weight: 700;
    line-height: 26px;
  }

  .mobile-navigation__data-hours {
    margin: 0 16px;
  }

  .mobile-navigation__data {
    width: 288px;
  }

  .mobile-navigation__figure {
    margin: 0 10px;
  }

  .mobile-navigation__text-oferta {
    margin: 0 25px;
  }
}
