.mobile-navigation__input-text {
  font-family:  "Open Sans", sans-serif;
  letter-spacing: -0.1px;
  margin: 0 auto;
  padding: 13px 0 12px 0;
  color: rgb(7, 12, 39);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  max-width: 328px;
}

@media screen and (max-width: 1025px) {
  .mobile-navigation__input-text {
    max-width: 456px;
    padding: 14px 0 12px 0;
    margin-left: 45px;
  }
}

@media screen and (max-width: 760px) {
  .mobile-navigation__input-text {
    margin-left: 0;
  }
}

@media screen and (max-width: 575px) {
  .mobile-navigation__input-text {
    line-height: 24px;
    margin-left: 25px;
  }
}
