/* CSS стили для menu-modal */

.menu-modal {
  display: block;
}

.menu-modal {
  display: block;
  box-sizing: border-box;
}

.menu-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  opacity: 0.3;
  transition: opacity 3s ease;
}

/* CSS стили для menu-modal__container */

.menu-modal__container {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  height: calc(100vh - 104px);
}

.menu-modal__content {
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 104px + 20px);
  min-width: 960px;
  padding: 15px;
  background: #fff;
  box-sizing: border-box;
  border-radius: 32px;
}

.menu-modal__header {
  display: flex;
  justify-content: space-between;
}

.menu-modal__close_button {
  width: 36px;
  height: 36px;
  background-color: #fff;
  background-image: url(../../images/close-icon.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: unset;
  border-radius: 50%;
}

.menu-modal__close_button:hover {
  background-color: #dadffa;
  cursor: pointer;
}

.menu-modal__back_container {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  border: none;
  background-color: #fff;
}

.menu-modal__back_container:hover {
  cursor: pointer;
}

.menu-modal__back_text {
  margin: 0;
  padding: 5px 0 0 0;
  font-family:  "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  text-align: center;
  color: #37489f;
}

.menu-modal__back_button {
  padding: 0;
  margin-top: 6px;
  margin-left: 5px;
  margin-right: 5px;
  width: 24px;
  height: 24px;
  background-color: #fff;
  background-image: url(../../images/base-marks.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: unset;
}

@media screen and (max-width: 1025px) {
  .menu-modal__content {
    min-width: 880px;
  }
}

@media screen and (max-width: 900px) {
  .menu-modal__content {
    min-width: 640px;
  }
}

@media screen and (max-width: 760px) {
  .menu-modal__content {
    min-width: 576px;
  }
}

@media screen and (max-width: 575px) {
  .menu-modal__content {
    min-width: 288px;
    width: 100vw;
  }
}
