/* CSS стили для menu-modal */

.menu-modal {
  display: block;
}

.menu-modal {
  display: block;
  box-sizing: border-box;
}

.menu-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  opacity: 0.3;
  transition: opacity 3s ease;
}

/* CSS стили для rent-subject-modal */

.rent-subject-modal__container {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  height: calc(100vh - 104px);
}

.rent-subject-modal__content {
  display: flex;
  flex-direction: column;
  min-width: 960px;
  height: calc(100vh - 104px);
  padding: 7px 10px;
  background: #fff;
  box-sizing: border-box;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
}

.rent-subject-modal__header {
  display: flex;
  justify-content: flex-end;
}

.rent-subject-modal__close_button {
  width: 48px;
  height: 48px;
  background-color: #fff;
  background-image: url(../../images/cross.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: unset;
  border-radius: 50%;
}

.rent-subject-modal__close_button:hover {
  background-color: #dadffa;
  cursor: pointer;
}

@media screen and (max-width: 1025px) {
  .rent-subject-modal__container {
    height: calc(100vh - 88px);
  }
  .rent-subject-modal__content {
    min-width: 880px;
    height: calc(100vh - 88px);
  }
}

@media screen and (max-width: 899px) {
  .rent-subject-modal__container {
    height: calc(100vh - 64px);
  }
  .rent-subject-modal__content {
    min-width: 680px;
    height: calc(100vh - 64px);
  }
}

@media screen and (max-width: 700px) {
  .rent-subject-modal__container {
    height: calc(100vh - 32px);
  }
  .rent-subject-modal__content {
    height: calc(100vh - 32px);
    min-width: 576px;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
  }
}

@media screen and (max-width: 575px) {
  .rent-subject-modal__content {
    padding: 7px 16px;
    min-width: 320px;
    width: 100vw;
  }
  .rent-subject-modal__close_button {
    margin: 10px 0;
    width: 35px;
    height: 35px;
  }
}
