.calendar {
  display: flex;
  flex-direction: row;
  position: absolute;
  z-index: 100000;
  top: 280px;
  left: 169px;
  width: 624px;
  height: 376px;
  margin: 4px 0 15px 0;
  padding: 0;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0px 8px 32px 0px #0d162129;
}

.calendar__month {
  width: 280px;
  margin: 20px 8px 20px 24px;
  padding: 0;
}

.calendar__month:nth-child(even) {
  width: 280px;
  margin: 20px 8px 20px 8px;
  padding: 0;
}

.calendar__restrict {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: absolute;
  z-index: 100000;
  top: 344px;
  width: 328px;
  height: 376px;
  margin: 4px 0 15px 0;
  padding: 0;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0px 8px 32px 0px #0D162129;
  left: 50%;
  transform: translateX(-50%);
  transition: left 0.5s ease, top 0.5s ease;
}

.calendar__month-restrict {
  width: 280px;
  margin: 20px 8px 20px 8px;
  padding: 0;
}

@media screen and (max-width: 1025px) {
  .calendar {
    top: 287px;
    left: 128px;
}

  .calendar__restrict {
    width: 456px;
    top: 345px;
  }
}

@media screen and (max-width: 900px) {
  .calendar {
    top: 296px;
    left: 8px;
  }
}

@media screen and (max-width: 760px) {
  .calendar {
    top: 330px;
    left: -25px;
  }
  .calendar__restrict {
    width: 496px;
    top: 345px;
  }
}

@media screen and (max-width: 700px) {
  .calendar__restrict {
    top: 345px;
  }
}

@media screen and (max-width: 575px) {
  .calendar__restrict {
    top: 345px;
    width: 318px;
  }

  .calendar__month-restrict {
    width: 280px;
    margin: 20px 0;
    padding: 0;
  }
}
