.subject-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subject-nav__title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  margin-top: 9px;
  padding-bottom: 16px;
  font-family: "Open Sans", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 32px;
  text-align: center;
  color: #070c27;
}

.border {
  border: solid 1px red;
}

/* CSS стили для cards */

.cards {
  margin: 0 auto;
  padding: 12px 20px 28px 20px;
  max-width: 934px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  gap: 16px 15px;
  box-sizing: border-box;
}

.cards__container {
  height: calc(100vh - 200px);
  overflow: auto;
  scrollbar-gutter: stable;
  scrollbar-width: none;
}

.cards__container::-webkit-scrollbar {
  width: 8px;
}

.cards__container::-webkit-scrollbar-track {
  background: rgba(13, 22, 33, 0.045);
}

.cards__container::-webkit-scrollbar-thumb {
  background: rgba(13, 22, 33, 0.15); /* Цвет ползунка */
  border-radius: 31px; /* Скругление углов */
}

@media screen and (max-width: 899px) {
  .cards {
    max-width: 632px;
    grid-template-columns: repeat(2, 1fr);
    padding: 12px 0 20px 0;
    margin: 0 14px 20px 14px;
  }
  .cards__container {
    scrollbar-width: none;
  }
  .subject-nav__title {
    padding-left: 39px;
  }
}

@media screen and (max-width: 700px) {
  .cards {
    max-width: 496px;
    grid-template-columns: repeat(2, 1fr);
    margin: 0 20px 20px 20px;
  }
  .cards__container {
    scrollbar-width: none;
  }
  .subject-nav__title {
    font-size: 24px;
    line-height: 28px;
    padding-left: 0;
  }
}

@media screen and (max-width: 600px) {
  .cards {
    max-width: 542px;
    margin: 0 7px;
  }
}

@media screen and (max-width: 575px) {
  .cards {
    grid-template-columns: repeat(1, 1fr);
    margin: 0 14px 15px 14px;
  }
}
