/* CSS стили для card */

.card {
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  box-shadow: 0px 16px 32px 0px #0d162129;
}

.card__figure {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

/* CSS стили для card__image */

.card__image {
  margin: 0;
  width: 100%;
  max-height: 146px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  object-fit: cover;
}

.card__figcaption {
  margin: 9px 0 16px 0;
  padding: 0;
  padding-left: 17px;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  font-family: "Open Sans", sans-serif;
  color: #070c27;
  word-wrap: break-word;
  word-break: break-word;
  white-space: normal;
}

.card__price {
  margin: 0;
  padding: 0;
  padding-left: 16px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
}

.card__price-number {
  margin: 10px 0;
  padding: 0;
  letter-spacing: 0.3px;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  color: #6073d5;
}

.card__book {
  display: flex;
  margin: 12px auto 15px auto;
  padding: 14px 62px 10px 62px;
  width: 256px;
  background-color: #6073d5;
  color: #fff;
  border: none;
  border-radius: 12px;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  text-align: center;
  justify-content: center;
}

.card__book:hover {
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .card__figcaption {
    margin-bottom: 14px;
  }

  .card__book {
    width: 230px;
  }
}

@media screen and (max-width: 899px) {
  .card__figcaption {
    padding-right: 5px;
  }
  .card__book {
    width: 275px;
  }
}

@media screen and (max-width: 700px) {
  .card__figcaption {
    padding-right: 5px;
  }
  .card__book {
    margin: 12px auto 15px auto;
    padding: 12px 26px 12px 26px;
    width: 208px;
  }
}

@media screen and (max-width: 600px) {
  .card {
    box-shadow: 0px 16px 10px 0px #0d162129;
  }
}

@media screen and (max-width: 575px) {
  .card {
    box-shadow: 0px 16px 32px 0px #0d162129;
  }
  .card__image {
    width: calc(100vw - 32px);
    max-height: 320px;
  }
  .card__book {
    width: calc(100vw - 64px);
  }
}

@media screen and (max-width: 360px) {
  .card__image {
    max-height: 146px;
  }
}
