.main__button {
  margin: 0;
  margin-top: 12px;
  margin-bottom: 37px;
  margin-left: 36px;
  padding: 0;
  border: 2px solid yellow;
  border-radius: 2px;
  color: 000;
  width: 358px;
  height: 50px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
  transition: 0.3s linear;
}

.main__button:hover {
  opacity: 0.8;
}
